ul#menu > li{float:left; list-style-type:none; position:relative; width: 100%;} 
ul#menu label{ position:relative;  display:block;  cursor:pointer;font-size: 13px; color: #747AA8;}
ul#menu label .fa{margin-right:5px;}
ul#menu label:after{
    content:"";
    position:absolute;
    display:block;
    top:12px;
    right:5px;
    width:0;
    height:0;
    border-top:4px solid rgba(255,255,255,.5);
    border-bottom:0 solid rgba(255,255,255,.5);
    border-left:4px solid transparent;
    border-right:4px solid transparent;
    transition:border-bottom .1s, border-top .1s .1s;
    }
  
    ul#menu  label:hover,
    ul#menu input:checked ~ label{}  
    ul#menu  input:checked ~ label:after{
    border-top:0 solid rgba(255,255,255,.5);
    border-bottom:4px solid rgba(255,255,255,.5);
    transition:border-top .1s, border-bottom .1s .1s;
  }

  ul#menu  input{display:none}
  ul.submenu{padding-left:25px;}

  ul#menu > li.active label, ul#menu > li.active a {
    color: #fff;
  }
  /*show the second levele menu of the selected voice*/
  ul#menu input:checked ~ ul.submenu {padding-left: 15px; max-height: 300px; transition: max-height 0.5s ease-in;border-left: 1px solid #30386d;;margin-bottom: 20px;}
  
  /*style for the second level menu*/
ul.submenu { max-height: 0;   overflow: hidden; list-style-type: none; box-shadow: 0 0 1px rgb(0 0 0 / 30%);transition: max-height 0.5s ease-out;min-width: 100%;}
ul.submenu li a {
  display: block;
  padding: 8px 0 10px 15px;
  color: #747AA8;
  text-decoration: none;
  transition: background .3s;
  white-space: nowrap;
  border-radius: 10px;
}
